import React from "react"
import { Link } from "gatsby"
import classnames from 'classnames'

import styles from './layout.module.css'
import img from "../../static/crab-cancer-symbol.png"

class Layout extends React.Component {

  render() {
    const { children, withFooter = true } = this.props;

    return (
      <div className={classnames(styles.root_panel)}>
        <div className={styles.grid_menu}>
          <div className={styles.menu}>
            <img className={styles.menu_item} src={img} alt={"menu logo"} />
            <div className={styles.menu_item}><Link to={"/"}>Home</Link></div>
            <div className={styles.menu_item}><Link to={"/calculators"}>Calculators</Link></div>
          </div>
        </div>
        <div className={styles.body_content}>
          <div className={styles.inside_content}>
            <div>
              {children}
            </div>
            {withFooter && (
              <footer>
                © {new Date().getFullYear()}, Martin Schnurer
              </footer>
            )}
          </div>
        </div>
      </div>
    )
  }
}

export default Layout
