import React from 'react'
import Layout from '../../../components/layout'
import SEO from "../../../components/seo"

class InflationCalculator extends React.Component {

  constructor() {
    super();
    this.state = {
      inputValue: 1000,
      inflationRate: 3,
      futureYear: new Date().getFullYear() + 5,
      error: null,
    }
  }

  changeInputValue = (e) => this.setState({ inputValue: e.target.value });
  changeInflationRate = (e) => this.setState({ inflationRate: e.target.value });
  changeFutureYear = (e) => this.setState({ futureYear: e.target.value });

  format = (val) => Math.round(val * 100) / 100;

  computeFutureValue = () => {
    const {inputValue, inflationRate, futureYear} = this.state;
    const yearNow = new Date().getFullYear();
    const percentualDecrease = 1 - (inflationRate/100);

    return this.format(inputValue * Math.pow(percentualDecrease,futureYear - yearNow));
  };

  getErrorMessage = () => {
    const {inputValue, inflationRate, futureYear} = this.state;
    const yearNow = new Date().getFullYear();
    if (futureYear <= yearNow) {
      return `Write down future year, (more than ${yearNow})`;
    }
    if (!inputValue) {
      return `Fill in input value`;
    }
    if (inflationRate === "") {
      return `Fill in inflation rate, for example 2% (Average is 1-2 %)`;
    }
    if (inflationRate < 0) {
      return `Inflation rate must be more than 0%`;
    }
    return null;
  };

  render() {
    const {inputValue, inflationRate, futureYear} = this.state;
    const yearNow = new Date().getFullYear();
    const errorMessage = this.getErrorMessage();
    return (
      <Layout>
        <SEO
          title={"Inflation money calculator"}
          description={"This calculator computes how much your money will be worth in the future"}
          keywords={["inflation", "money", "calculator", "investing"]}
        />
        <div>
          <div className="topbottom_margined">
            Inflation is the yearly rate of decreasing monetary values.
          </div>
          <div>Value in {yearNow}: <input value={inputValue} onChange={this.changeInputValue} /> €</div>
          <div>Inflation Rate: <input value={inflationRate} onChange={this.changeInflationRate} />%</div>
          <div>Future year: <input value={futureYear} onChange={this.changeFutureYear} placeholder={"2020"} /></div>
          {
            errorMessage ? (
              <div className={"error"}>{errorMessage}</div>
            ): (
              <h2>Your {inputValue} € will be worth <b>{this.computeFutureValue()} €</b> in {futureYear}</h2>
            )
          }
        </div>
      </Layout>
    )
  }
}

export default InflationCalculator
